import React from 'react';
import $ from 'jquery';
import './Content.css';
var accesstoken = "kdAsj5SsBKO4gyYsFfMgrN_gPHZPeKI9Ve4KHG33upRjJQqpciAlh1IF3DmQOW-b";

export default class extends React.Component {
    constructor(props){
        super();
        this.state ={
            search: props.search
        }
    }

    componentWillReceiveProps(props){
        $("#results-holder").empty();
        var usersearch = props.search
        var limit = 5;
        var queryURL = "https://api.genius.com/search?q=" + usersearch + "&access_token=" + accesstoken;
        $.ajax({
            url: queryURL,
            method: "GET",
        }).then(function (response1) {
            console.log(response1.response.hits.length);
            console.log(response1);
            if (limit>=response1.response.hits.length){
                limit=response1.response.hits.length;
            }
            for (var i = 0; i < limit; i++) {
                
                var title = response1.response.hits[i].result.full_title;
                var lyrpath = response1.response.hits[i].result.path;
                var primaryartist = response1.response.hits[i].result.primary_artist.name;
                var song = title.split("by")[0].trim();
                var artist = title.split("by")[1].trim();
                var imgurl=response1.response.hits[i].result.header_image_url;
                console.log(artist + " : " + song);
                var btns = $("<button>");
                btns.addClass("song-button btn btn-dark m-1");
                btns.attr("title", title);
                btns.attr("songname",song);
                btns.attr("artistname",artist);
                btns.attr("lyrpath",lyrpath);
                btns.attr("primaryartist", primaryartist);
                btns.attr("imgurl", imgurl);
                btns.text(artist + ":-" + song)
                $("#results-holder").append(btns); 
            }
        });
    }

    render(){
        if(this.props.search === ""){
            return(
            <div className="card"><img className="card-img-left" src="https://via.placeholder.com/150" alt="placeholder" />
            <div className="card-body">
                <h3 className="card-title">S A M P L E C O N T E N T</h3>
                <p className="card-text text-muted">Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nam blandit ac dolor vel malesuada. Quisque tincidunt bibendum semper. Fusce vitae congue nisl, eget dictum ipsum. Sed id volutpat urna. Vestibulum ligula urna, posuere ut purus quis, rhoncus placerat dolor. Etiam mi purus, viverra sed aliquet non, ornare id mi. Nullam et semper risus. Donec massa diam, lobortis ut pulvinar at, bibendum in tellus. Pellentesque dapibus, dolor at tempor pharetra, odio neque congue eros, eget blandit diam augue ut eros. Suspendisse fringilla dui in felis vestibulum sodales. Morbi non lacus purus.</p>
                <button id="youtube button" className="btn btn-danger">YouTube Video</button>
            </div>
        </div>
            )}
        else{return(
            <div className="mainContent">
                <div className="card content-card">
                <div id="results-holder" className="btn-group mr-2 flex-wrap" role="group"></div>
                </div>
            </div>
    )}}
}